@import url('https://fonts.googleapis.com/css2?family=Open+Sans:ital,wght@0,300..800;1,300..800&display=swap');

.main {
    flex :1 ;
    min-height: 100vh;
    padding-bottom: 15vh;
    position: relative;
}

.main .nav {
    display: flex;
    align-items: center;
    justify-content: space-between;
    font-size: 22px;
    padding: 20px;
    color : #585858;
}

.main .nav img {
    width : 50px;
    border-radius: 50%;
}

.main-container {
    max-width: 900px;
    margin: auto;
}

.main .greet {
   
    font-size: 48px;
    color : #c4c7c5;
    font-weight: 500;
    margin : 6px 0px;
}

.main .greet span {
    background: -webkit-linear-gradient(16deg , #4b90ff , #ff5546);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
}

.main .cards {
    display: grid;
    grid-template-columns: repeat(auto-fill,minmax(180px,1fr));
    gap : 18px;
    padding: 20px;
}

.main .card {
    height : 200px;
    padding: 15px;
    background-color: #f0f4f9;
    border-radius: 10px;
    position: relative;
    cursor: pointer;
}

.main .card img {
    width : 35px;
    padding: 5px;
    position: absolute;
    background-color: white;
    border-radius: 20px;
    bottom : 10px;
    right : 10px;
}

.main .card p {
    color : #585858;
    font-size: 17px;
}

.main .card:hover {
    background-color: #dfe4ea;
}

.main-bottom {
    position: absolute;
    bottom : 0;
    width: 100%;
    max-width: 900px;
    padding: 0px 20px;
    margin: auto;
}

.search-box {
    display: flex;
    align-items: center;
    justify-content: space-between;
    gap: 20px;
    background-color: #f0f4f9;
    padding: 10px 20px;
    border-radius: 50px;
}

.search-box img {
    width : 25px;
    cursor: pointer;
    
}

.search-box input {
    flex: 1;
    background: transparent;
    border: none;
    outline: none;
    padding: 8px;
    font-size: 18px;
}

.search-box div {
    display: flex;
    align-items: center;
    gap: 15px;
}


.main .bottom-info {
     font-size: 13px;
     margin: 4px auto;
     text-align: center;
     font-weight: 300;
}

.result {
    padding : 0px 5%;
    max-height: 70vh;
    overflow-y: scroll;
}

.result::-webkit-scrollbar {
    display: none;
}

.result-title {
    margin: 20px 0px;
    display: flex;
    align-items: center;
    gap: 20px;
}

.result img {
    width: 40px;
    border-radius: 50%;
}

.result-data {
    display: flex;
    align-items: start;
    gap: 10px;
}

.loader {
    width : 100%;
    display: flex;
    flex-direction: column;
    gap: 4px;
}

.loader hr {
    border-radius: 4px;
    border: none;
    background-color: #f6f7f8;
    background: linear-gradient(to right , #9ed7ff , #ffffff, #9ed7ff);
    background-size: 800px 50px;
    height: 20px;
    animation: loader 3s  infinite linear;
}

@keyframes loader {

    0% {
        background-position: -800px 0px;
    }

    100% {
    background-position: 800px 0px;
    }
    
}

.result-data p {
    font-size: 16px;
    color : #1F1F1F;
    /* font-weight: 500; */
    line-height: 1.2;
    font-family: "Open Sans", sans-serif;
    font-optical-sizing: auto;
    font-weight: 400;
    font-style: normal;
    font-variation-settings:
      "wdth" 100;
}

@media (max-width : 600px) {
    .main-bottom input {
        flex : none;
        width : 150px;
    }

    .main-bottom img {
        width : 20px;
    }

    .search-box {
        padding: 5px 10px;
    }

    .search-box div {
        gap : 5px;
    }
}

